$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

.item {
  display: -webkit-flex;
  display: flex;
  width: 100%;

  -webkit-align-items: center;

          align-items: center;
  .icon img {
    width: 24px;
    height: 27px;
  }
  .photo {
    position: relative;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 10;
      border-radius: 50%;
    }
  }
}
.grade {
  display: inline-block;
  width: 40px;
  height: 20px;
  border-radius: 2px;
}
.first {
  .icon {
    margin-right: 65px;
  }
  .top {
    position: relative;
    .photo {
      width: 146px;
      height: 146px;
      img {
        width: 96px;
        height: 96px;
        margin-top: -48px;
        margin-left: -48px;
      }
    }
    .bg {
      position: relative;
      z-index: 99;
      display: -webkit-flex;
      display: flex;
      width: 146px;
      height: 146px;
      background: url("../../../asset/img/ranking/top_1@2x.png");

      -webkit-justify-content: center;

              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
    }
    .info {
      width: 100%;
      margin-top: 8px;
      .name {
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: 20px;

        -webkit-align-items: center;

                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
        span {
          max-width: 110px;
          margin-right: 5px;
          font-size: 14px;
          font-weight: 400;
          color: rgba(53, 53, 53, 1);
        }
      }
      .assets {
        display: -webkit-flex;
        display: flex;
        width: 100%;
        height: 20px;
        color: rgba(53, 53, 53, 1);

        -webkit-align-items: center;

                align-items: center;
        -webkit-justify-content: center;
                justify-content: center;
      }
    }
  }
}
.second,
.third {
  height: 131px;

  .photo {
    position: relative;
    display: -webkit-flex;
    display: flex;
    width: 110px;
    height: 100%;

    -webkit-align-items: center;

            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .info {
    width: 105px;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    color: rgba(53, 53, 53, 1);

    -webkit-align-items: center;

            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    .name {
      display: -webkit-box;
      max-height: 38.5px;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
    }
    .assets {
      height: 14px;
      margin-top: 5px;
    }
  }
}
