$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}


.index_ranking {
  .icon img {
    width: 24px;
    height: 32px;
  }
  .ranking_list {
    float: right;
    background-color: #fff;

    .index_rank {
      width: 360px;
      margin-right: 0;
      .title {
        position: absolute;
      }
      .ant-tabs-ink-bar {
        display: none!important;
      }
      .ant-tabs-bar {
        border-bottom: none;
        .ant-tabs-extra-content {
          padding: 2px;
          margin-right: 20px;
          a {
            color: #606162;
          }
        }
      }
      .ant-tabs-tab {
        padding-right: 12px;
        margin: 0;
        font-size: 14px;
        line-height: 1.5;
      }
      .ant-tabs-nav {
        float: right;
      }
      .ant-tabs-tab:after {
        position: absolute;
        left: 0;
        content: 'Â·';
      }
      .ant-tabs-tab:hover {
        color: $red;
      }
      .ant-tabs-tab:first-of-type:after {
        content: '';
      }
      .ant-tabs-tab-active {
        color: $red;
      }
      .ant-tabs-tab-active:hover {
        color: #000;
      }
    }
    .anchor {
      .border {
        width:316px;
        height:2px;
        margin: 0 auto;
        margin-top: 26px;
        background:rgba(241,242,244,1);
      }
    }
  }
}





.section {
  position: relative;
  display: inline-block;
  width: 368px;
  margin-right: 30px;
  margin-bottom: 30px;
  .title {
    display: -webkit-flex;
    display: flex;
    height: 45px;
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    line-height: 45px;
    color: black;

    -webkit-align-items: center;

            align-items: center;
    span {
      display: inline-block;
      width: 15px;
      height: 23px;
    }
  }
  .notice {
    display: -webkit-flex;
    display: flex;
    height: 37px;
    font-size: 13px;
    font-weight: 400;
    color: $white;

    -webkit-align-items: center;

            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  .list {
    padding: 0 28px;
  }
  .first-avatar {
    width: 112px;
    height: 112px;
    margin-top: -55px;
    margin-left: -56px;
  }
  .second-avatar {
    width: 88px;
    height: 88px;
    margin-top: -45.5px;
    margin-left: -44px;
  }
  .third-avatar {
    width: 80px;
    height: 80px;
    margin-top: -43px;
    margin-left: -40px;
  }
  .other-avatar {
    width: 60px;
    height: 60px;
    margin-top: -30px;
    margin-left: -30px;
  }
}
